<template>
  <div id="app">
    <InfoCivi :info-civi="ObtenerCivi()" :class="ClaseInfoCivi()" />
    <router-view/>
  </div>
</template>

<script>
  // import Firebase from 'firebase';
  import Firebase from 'firebase/app';
  import 'firebase/database';
  import Config from "./Config";
  import InfoCivi from './components/InfoCivi';

  let app = Firebase.initializeApp(Config);
  //Firebase.analytics();
  let db = app.database();
  let CivsInfoRef = db.ref("CivsInfo");

export default {
  name: 'App',
  components: {
    InfoCivi
  },
  data(){
      return {
          InfoCivis: null,
          IdCivi: null,
          IdSteam: "",
          InfoUltimoPartido: null
      }
  },
  async mounted(){
    this.InfoCivis = await this.ObtenerInfoCivilizaciones();
    //console.log(this.InfoCivis);
    // this.IdCivi1 = 8;
    // this.IdCivi2 = 26;

    this.IdSteam = this.$route.query.idsteam;
    if(!this.IdSteam){
        return;
    }
    
    await this.SetearInfoAge();
    this.TrabajarInfoUltimoPartido();
  },
  methods: {
    async ObtenerInfoCivilizaciones(){
        var _Retorno = {};
        try {
          await CivsInfoRef.once('value', function(info) {
              info.forEach(function(Civi) {
                  _Retorno[Civi.val().Id] = Civi.val();
              });
          });
        } catch (e) {
          console.log(e);
        }
        return _Retorno;
    },
    async SetearInfoAge(){
        try {
        const _Promesa = await fetch("https://aoe2.net/api/strings?game=aoe2de&language=sp");
        const _Resultado = await _Promesa.json();
        
        this.Civis = _Resultado.civ;
        this.TiposMapa = _Resultado.map_type;
        } catch (error) {
        console.log(error);
        }
    },
    async TrabajarInfoUltimoPartido(){
        var _This = this;
        const _Promesa = await fetch("https://aoe2.net/api/player/lastmatch?game=aoe2de&steam_id=" + _This.IdSteam);
        let _Resultado = await _Promesa.json();
        
        //Retorno si no es un partido 1v1
        if (_Resultado.last_match.num_players > 2){
          console.log("Last match is not 1v1");
          setTimeout(() => { this.TrabajarInfoUltimoPartido(); }, 10 * 1000);
          return;
        }

        //Retorno si ya mostre la info
        if(this.InfoUltimoPartido != null){
            if (this.InfoUltimoPartido.match_id == _Resultado.last_match.match_id){
              setTimeout(() => { this.TrabajarInfoUltimoPartido(); }, 10 * 1000);
              return;
            }
        }

        this.InfoUltimoPartido = _Resultado.last_match;
        //console.log(this.InfoUltimoPartido);
        var _PlayersOrdenados = [];
        for (let i = 0; i < this.InfoUltimoPartido.num_players; i++) {
          let _P = this.InfoUltimoPartido.players[i];
          if(_P.steam_id == _This.IdSteam){
            _PlayersOrdenados[0] = _P;
            document.title = _P.name + " - Overlay Info Civs - Aoe II DE";
          }else{
            _PlayersOrdenados[1] = _P;
          }
        }

        console.log("A new game is starting...", this.InfoUltimoPartido);
        this.IniciarProcesoParaMostrarInfoCivis(_PlayersOrdenados);
        
        setTimeout(() => { 
          this.TrabajarInfoUltimoPartido(); 
          }, 10 * 1000);
    },
    IniciarProcesoParaMostrarInfoCivis(_PlayersOrdenados){
      this.IdCivi = _PlayersOrdenados[0].civ;
      console.log(this.InfoCivis[this.IdCivi].Name, this.ObtenerCivi());
      const _DuracionInfo = 20000;
      let _Duracion = _DuracionInfo;
      setTimeout(() => {
        this.IdCivi = null;
      }, _Duracion);
      _Duracion += 4000;
      if(_PlayersOrdenados[0].civ != _PlayersOrdenados[1].civ){
        setTimeout(() => {
          this.IdCivi = _PlayersOrdenados[1].civ;
          console.log(this.InfoCivis[this.IdCivi].Name, this.ObtenerCivi());
        }, _Duracion);
        _Duracion += _DuracionInfo;
        setTimeout(() => {
          this.IdCivi = null;
        }, _Duracion);
      }
    },
    ClaseInfoCivi(){
      var _Retorno = "InfoCivi";
      if(this.IdCivi){
        _Retorno += " Visible";
      }
      return _Retorno
    },
    ObtenerCivi(){
      if(this.InfoCivis && this.IdCivi){
        //console.log(this.IdCivi1);
        return this.InfoCivis[this.IdCivi];
      }
      return null;
    }
  }
}
</script>

